* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

ul {
  padding-left: 10px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

 /* Cards templates  */

.card__template--full {
  display: flex;
  align-items: center;
}

.card__template--mini {
  display: flex;
  align-items: center;
}

.card__item {
  list-style: none;
  text-align: start;
  margin-bottom: 10px;
}
.card__title {
  margin-left: 15px;
  margin: 0;
  margin-left: 15px;
  display: inline-block;
}
.card__paragraph {
  font-size: 23px;
  font-weight: 500;
  margin: 0;
}

.card__paragraph-text{
  font-size: 23px;
  font-weight: 700;
}

.card-list {
  padding: 0;
  margin: 0;
  margin-top: 30px;
}
